import './Doctors.css'
import Card from '../PatientComponents/Card';
import {getdoctors} from './Doctorslist';
import {useState,useEffect} from 'react';

const Doctors = () => {
    const [doctors,setdoctors] = useState([]);
    const [displaydocs,setDisplaydocs] = useState([]);
    const [job,setjob]=useState("all");


    const filterbydesignation =() =>{
               if(job==="all"){return setDisplaydocs(doctors);}

        const temp = doctors.filter(f=>f.designation===job)
        setDisplaydocs(temp);
    }

    useEffect(() => {
        async function  getdata (){
            let d= await getdoctors();
            console.log(process.env.PUBLIC_URL);
       
            setDisplaydocs(d);
            setdoctors(d);
        }
        getdata();
       
        
      
    }, []);
    

    const [filterid,setFilterid] = useState('');
    
    const filter = () =>{
        let id=filterid.trim();
        let temp=[]
        if(id===''){
        filterbydesignation();
    }
    else if(!isNaN(parseFloat(id)) && isFinite(id)){
        temp = doctors.filter(d=>d.id.search(`${id}`)!==-1);
        setDisplaydocs(temp);
        // filterbydesignation(displaydocs);

        
    }
    else{
        temp = doctors.filter(d=>{
            let k=d.name.toLowerCase();
            id = id.toLowerCase();
            return k.search(id)!==-1;
        });
        setDisplaydocs(temp);
    }
    }
    
    return (
        doctors.length>0?
        <div className="doctors">
            <search className="filter">
                <input type="text" value={filterid} onChange={e=>setFilterid(e.target.value)} placeholder="Search by id or name"/>
                <button onClick={filterid!==''?filter:null}>Search</button>
                <select onChange={(e)=>setjob(e.target.value)}>
                    <option value="all">All</option>
                    <option value="Physician">Physician</option>
                    <option value="Neurologist">Neurologist</option>
                    <option value="Cardiologist">Cardiologist</option>
                </select>
                <button onClick={filterbydesignation}>filter</button>
            </search>

            <display className="cards">
               {
                   displaydocs.map(doctor=> <Card key={doctor.id} data={doctor} />)
               }
                

            </display>
            
        </div>:null
    )
}

export default Doctors
