import './Card.css';

const Card = ({data}) => {
    return (
        <div className="card">
            <div className="upper">
                <img src={data.imageUrl} alt="" />
            </div>
            <div className="lower">
                <p>Name : {data.name}<sub>{data.degree}</sub></p>
                <p>Id: {data.id}</p>
                <p>Designation: {data.designation}</p>
                <button>View Profile</button>
            </div>
        </div>
    )
}

export default Card
