import React,{useState} from 'react'
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom';
import Doctors from './PatientScreens/Doctors'
import PatientHome from './PatientScreens/PatientHome';
import Doctor from './PatientScreens/Doctor';
import History from './PatientScreens/History';
import ErrorPage from './PatientScreens/ErrorPage';
import NavBar from './PatientComponents/NavBar';

import BackDrop from './PatientComponents/BackDrop'
import SideDrawer from './PatientComponents/SideDrawer'
const Patient = () => {
    const [sideToggle, setsideToggle] = useState(false);
    return (
        <div>
           
            <Router>
            <NavBar click={()=> setsideToggle(true)} />
            <SideDrawer show={sideToggle} click={()=>setsideToggle(false)}/>
            <BackDrop show={sideToggle} click={()=>setsideToggle(false)}/>
            <main>
            <Switch>

<Route path = {process.env.PUBLIC_URL +'/'} exact component={PatientHome} />
<Route path ={process.env.PUBLIC_URL + '/Appointment'} exact component = {Doctors} />
<Route path = {process.env.PUBLIC_URL +'/Doctor/:id'}   component = {Doctor} />
<Route path ={process.env.PUBLIC_URL +'/History'}  exact component = {History} />
<Route component={ErrorPage} />



</Switch>

            </main>
              


            </Router>

           
        </div>
    )
}

export default Patient
