import './NavBar.css';
import {Link} from 'react-router-dom';

const NavBar = ({click}) => {
    return (
        <nav className="navBar">
            
            <div className="navBar_logo">
                <h2>Neural Hack</h2>
            </div>


            
            <ul className="navBar_links">
            <li>
                <Link to="/">
               
              Home
               
               </Link>
                </li>
                <li>
                <Link to="/Appointment"className="cart_link">
               <i className="fas fa-handshake-o"></i>
               <span className="">Appointment</span>
               
              
               </Link>
                </li>
                <li>
                <Link to="/History">
               
               Records
               
               </Link>
                </li>
             
            </ul>

            
            <div className="navBar_hamburger" onClick={click}>
               
            <div></div>
            <div></div>
            <div></div>
            </div>

        </nav>
    )
}

export default NavBar
